import Question from "../components/Question";
import { useSelector, useDispatch } from 'react-redux';
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import {setNewCurrentQuestion} from '../app/questionsSlice';

export default function HomePage() {

  const question = useSelector((state) => state.questions.currentQuestion);
  const questionsLoaded = useSelector((state) => state.questions.questionsLoaded);
  const mode = useSelector((state) => state.settings.mode);
  const testType = useSelector((state) => state.settings.testType);
  const questions = useSelector((state) => state.questions);
  const questionIndex = useSelector((state) => state.questions.questionIndex);    
  const dispatch = useDispatch();
  const questionsCount = questions[testType].length;;
  
  if(!question || questionsLoaded === false) {
    return (
      <div> 
        Loading questions... 
      </div>
    );
  }

  const handleNext = () => {
    const newIndex = questionIndex + 1;
    const newQuestion = questions[testType][newIndex];

    dispatch(setNewCurrentQuestion({ questionIndex: newIndex, question: newQuestion }));
  }

  const handlePrevious = () => {
    const newIndex = questionIndex - 1;
    const newQuestion = questions[testType][newIndex];

    dispatch(setNewCurrentQuestion({ questionIndex: newIndex, question: newQuestion }));
  }

  return (

    <main>
      <div className="max-w-7xl mx-auto py-6 sm:px-6 lg:px-8">      
        <div className="px-4 py-4 sm:px-0">

          <Question 
              question={question} 
              questions={questions} 
              questionIndex={questionIndex}
          />

          {mode === "learn" && (
            <Stack 
              direction="row" 
              spacing={2} 
              justifyContent="center"    
              className="mt-4"       
            >
              <Button 
                variant="contained" 
                disabled={questionIndex === 0} 
                style={{ width:150 }} 
                onClick={handlePrevious}>
                  Previous
              </Button>
              <Button 
                variant="contained" 
                style={{ width:150 }} 
                disabled={questionIndex === questionsCount - 1}
                onClick={handleNext}>
                  Next
              </Button>
            </Stack>
          )}

        </div>
      </div>
    </main>              

  );
}