
import React, { useState } from 'react';
import QuestionAnswer from "./QuestionAnswer";
import {incrementrightAnswers, incrementWrongAnswers} from '../app/testSlice';
import {setNewCurrentQuestion} from '../app/questionsSlice';
import { useSelector, useDispatch } from 'react-redux';


export default function Question({ question, questions, questionIndex }) {

    const locale = useSelector((state) => state.settings.locale);
    const testType = useSelector((state) => state.settings.testType);
    const selectionMethod = useSelector((state) => state.settings.selectionMethod);
    const [showCorrectAnswer, setShowCorrectAnswer] = useState(false);
    const [answered, setAnswered] = useState(false);
    const dispatch = useDispatch();
    const answers = question[locale].answers;

    const handleAnswer = (answer) => {  

        if(!answered) {
            setAnswered(true);
            setShowCorrectAnswer(true);

            if(answer.is_correct) {
                dispatch(incrementrightAnswers());
            }
            else {
                dispatch(incrementWrongAnswers());
            }                                    

            setTimeout(() => {
                setAnswered(false);                                    
                setShowCorrectAnswer(false);

                let newQuestionIndex = questionIndex + 1;
                let newQuestion = questions[testType][newQuestionIndex];                    

                if(selectionMethod === "random") {
                    const randomIndex =  Math.floor(Math.random() * questions[testType].length);  
                    newQuestion = questions[testType][randomIndex];
                    newQuestionIndex = randomIndex;
                }

                dispatch(setNewCurrentQuestion({ questionIndex: newQuestionIndex, question: newQuestion }));
            } , 1000);
        }   
    }

    return (    
        <div className="p-4 bg-white rounded-lg border shadow-md sm:p-6 dark:bg-gray-800 dark:border-gray-700">
            <span className="text-sm text-gray-500 dark:text-gray-400">{question.id}</span>
            <h5 className="mb-3 text-base font-semibold text-gray-900 lg:text-xl dark:text-white">
                { question[locale].text }
            </h5>        
            <ul className="my-4 space-y-3">
                { answers.map((answer, index) => (
                    <li key={index}>
                        <QuestionAnswer 
                            answer={answer} 
                            showCorrectAnswer={showCorrectAnswer}
                            onClick={() => handleAnswer(answer)} 
                        />
                    </li> 
                )) }           
            </ul>

        </div>
    );
}