
import * as React from 'react';
import Box from '@mui/material/Box';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import { useSelector, useDispatch } from 'react-redux';
import { setProperty } from '../app/settingsSlice';
import { setQuestionIndex } from '../app/questionsSlice';
import { Button, TextField } from '@mui/material';

export default function SettingsPage() {
    
    const dispatch = useDispatch();
    const locale = useSelector((state) => state.settings.locale);
    const testType = useSelector((state) => state.settings.testType);
    const selectionMethod = useSelector((state) => state.settings.selectionMethod);    
    const mode = useSelector((state) => state.settings.mode);
    const questionIndex = useSelector((state) => state.questions.questionIndex);

    const handleChange = (event) => {
        dispatch(setProperty({prop: event.target.name, value: event.target.value})); 
    };


    return (
        <div>

            <header class="bg-white shadow">
                <div class="max-w-7xl mx-auto py-6 px-4 sm:px-6 lg:px-8">
                    <h1 class="text-3xl font-bold text-gray-900">Settings</h1>
                </div>
            </header>

            <main>
                <div className="max-w-7xl mx-auto py-6 sm:px-6 lg:px-8">      
                    <div className="px-4 py-4 sm:px-0">                    

                        {/* <Box mt={2}>
                            <FormControl fullWidth>
                                <InputLabel id="select-language-label">Language</InputLabel>
                                <Select
                                    labelId="select-language-label"
                                    id="locale"
                                    value={locale}
                                    label="Language"
                                    onChange={handleChange}
                                    name="locale"
                                >
                                    <MenuItem value={"en"}>English</MenuItem>
                                    <MenuItem value={"fr"}>Français</MenuItem>
                                </Select>
                            </FormControl>
                        </Box> */}
                        <Box mt={2}>
                            <FormControl fullWidth>
                                <InputLabel id="select-testType-label">Type of questions</InputLabel>
                                <Select
                                    labelId="select-testType-label"
                                    id="testType"
                                    value={testType}
                                    label="Type of questions"
                                    onChange={handleChange}
                                    name="testType"
                                >
                                    <MenuItem value={"basic"}>Basic questions</MenuItem>
                                    <MenuItem value={"advanced"}>Advanced questions</MenuItem>
                                </Select>
                            </FormControl>                                            
                        </Box>

                        <Box mt={2}>
                            <FormControl fullWidth>
                                <InputLabel id="select-selectionMethod-label">Type of questions</InputLabel>
                                <Select
                                    labelId="select-selectionMethod-label"
                                    id="selectionMethod"
                                    value={selectionMethod}
                                    label="Type of questions"
                                    onChange={handleChange}
                                    name="selectionMethod"
                                >
                                    <MenuItem value={"random"}>Random</MenuItem>
                                    <MenuItem value={"sequential"}>Sequential</MenuItem>
                                </Select>
                            </FormControl>                                            
                        </Box>

                        <Box mt={2}>
                            <FormControl fullWidth>
                                <InputLabel id="select-mode-label">Mode</InputLabel>
                                <Select
                                    labelId="select-mode-label"
                                    id="mode"
                                    value={mode}
                                    label="Mode"
                                    onChange={handleChange}
                                    name="mode"
                                >
                                    <MenuItem value={"test"}>Test</MenuItem>
                                    <MenuItem value={"learn"}>Learn</MenuItem>
                                </Select>
                            </FormControl>                                            
                        </Box>     


                        <Box mt={2}>
                            <FormControl fullWidth>
                                {/* <InputLabel id="select-questionIndex-label">Question index</InputLabel> */}
                                <TextField 
                                    variant="outlined"
                                    label="Question index"
                                    value={questionIndex} 
                                    onChange={(e) => {  
                                        if(e.target.value){
                                            dispatch(setQuestionIndex(e.target.value));
                                        }
                                    }} 
                                    inputProps={{ min:0 }}
                                    name="questionIndex" 
                                    id="questionIndex" 
                                    type="number" 
                                />
                            </FormControl>                                            
                        </Box>                                            

                    </div>
                </div>
            </main>            

        </div>
    );
}