import { NavLink } from "react-router-dom";
import { useTranslation } from 'react-i18next';

export default function NavBar() {

    const { t, i18n } = useTranslation();

    return (
        <nav className="bg-gray-800">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex items-center justify-between h-16">
            <div className="flex items-center">
              <div className="grow">
                <a href="https://andrekgb.github.io/canadian_amateur_radio_exam/">
                    <img src="https://upload.wikimedia.org/wikipedia/commons/c/cf/Flag_of_Canada.svg" alt="Canada" className="h-8 w-auto" />
                </a>
              </div>
              
              <div className="ml-10 flex items-baseline space-x-4">

              <NavLink
                    to="/questions"
                    className={isActive =>
                        (!isActive ? "bg-gray-900 " : "bg-red-900") + " text-white px-3 py-2 rounded-md text-sm font-medium"
                    }
                >
                    Questions
                </NavLink>

                <NavLink
                    to="/settings"
                    className={isActive =>
                        (!isActive ? "bg-gray-900 " : "bg-red-900") + " text-white px-3 py-2 rounded-md text-sm font-medium"
                    }
                >
                    Settings
                </NavLink>

                <NavLink
                    to="/about"
                    className={isActive =>
                        (!isActive ? "bg-gray-900 " : "bg-red-900") + " text-white px-3 py-2 rounded-md text-sm font-medium"
                    }
                >
                    About
                </NavLink>
              </div>
              
            </div>
          </div>
        </div>
      </nav>
    );
}