import './App.css';
import NavBar from './components/NavBar';
import HomePage from './pages/HomePage';
import AboutPage from './pages/AboutPage';
import SettingsPage from './pages/SettingsPage';
import {BrowserRouter as Router,Route,Switch} from "react-router-dom";
import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import {setQuestions} from './app/questionsSlice';
import myQuestions from './app/questions.json';
import _ from 'underscore';

const treatQuestion = (question) => {

  const clone = JSON.parse(JSON.stringify(question));
  clone.en.answers = _.shuffle(clone.en.answers);
  clone.fr.answers = _.shuffle(clone.fr.answers);
  return clone;
};

function App() {

  const dispatch = useDispatch();

  // Load questions on app load
  useEffect(()=>{

    const questions = {
      basic: [],
      advanced: []
    }
    
    myQuestions.basic.map((question) => {
      questions.basic.push(treatQuestion(question));
    }); 

    myQuestions.advanced.map((question) => {
      questions.advanced.push(treatQuestion(question));
    });

    dispatch(setQuestions(questions));   
  }, []);

  return (
    <Router>
      <div className="min-h-full">
        <NavBar />
        {/* <main>
          <div className="max-w-7xl mx-auto py-6 sm:px-6 lg:px-8">      
            <div className="px-4 py-4 sm:px-0">       */}

            <Switch>
              <Route path="/about">
                <AboutPage />
              </Route>
              <Route path="/settings">
                <SettingsPage />
              </Route>
              <Route path="/questions">
                <HomePage />
              </Route>
              <Route path="/">
                <HomePage />
              </Route>
            </Switch>

                
            {/* </div>
          </div>
        </main> */}
      </div>
    </Router>
  );
}

export default App;
