export function getItem (key, defaultValue) {
      
    try {
  
        if(localStorage.getItem(key) === null){
            return defaultValue;
        }
        else {
            return JSON.parse(localStorage.getItem(key));        
        }        
    } catch (error) {
        console.error(error);
        return defaultValue;
    }
  }


  export function setItem(key, value) {
    try {
        localStorage.setItem(key, JSON.stringify(value));   
    } catch (error) {
        console.error(error);
    } 
  }