import { getItem, setItem } from './helperFunctions';  
import { createSlice } from '@reduxjs/toolkit'

const initialState = {
    totalQuestions: getItem('totalQuestions', 0),
    totalAnswered: getItem('totalAnswered', 0),
    rightAnswers: getItem('rightAnswers', 0),
    wrongAnswers: getItem('wrongAnswers', 0),
    questionIndex: getItem('wrongAnswers', 0),
}

export const testSlice = createSlice({
  name: 'test',
  initialState,
  reducers: {
    setTotalQuestions: (state, action) => {
      state.totalQuestions = action.payload;
      setItem('totalQuestions', action.payload);
    },
    setTotalAnswered: (state, action) => {
        state.totalAnswered = action.payload;
        setItem('totalAnswered', action.payload);
    },    
    incrementTotalAnswered: (state) => {
      state.totalAnswered ++;
      setItem('totalAnswered', state.totalAnswered);
    },
    incrementrightAnswers: (state) => {
        state.rightAnswers ++;
        setItem('rightAnswers', state.rightAnswers);
    },    
    incrementWrongAnswers: (state) => {
        state.wrongAnswers ++;
        setItem('wrongAnswers', state.wrongAnswers);
    },        
  },
})

// Action creators are generated for each case reducer function
export const { 
    setTotalQuestions, 
    setTotalAnswered, 
    incrementTotalAnswered,
    incrementrightAnswers,
    incrementWrongAnswers
} = testSlice.actions

export default testSlice.reducer