
import { createSlice } from '@reduxjs/toolkit'
import { getItem, setItem } from './helperFunctions';  

const initialState = {
    basic:[],
    advanced:[],
    currentQuestion:null,
    questionIndex: getItem('questionIndex', 0),
    questionsLoaded:false,
}

export const questionsSlice = createSlice({
  name: 'questions',
  initialState,
  reducers: {
    setQuestions: (state, action) => {
      
      const { basic, advanced } = action.payload;
      const selectionMethod = getItem('selectionMethod', 'sequential');
      const testType = getItem('testType', 'basic');

      let hasQuestions =  false;

      if(basic !== null && basic.length > 0 && advanced !== null && advanced.length > 0) {
        state.questionsLoaded = true;
        hasQuestions = true;
      }

      if(hasQuestions) {
        
        state.basic = basic;
        state.advanced = advanced;      
  
        if(selectionMethod === 'random'){        
          const randomIndex = Math.floor(Math.random() * action.payload[testType].length);
          state.currentQuestion = action.payload[testType][randomIndex];
          state.questionIndex = randomIndex;
          setItem('questionIndex', randomIndex);
        }
        else {
          state.currentQuestion = action.payload[testType][0];
          state.questionIndex = 0;
          setItem('questionIndex', 0);
        }
      }
    },  
    setNewCurrentQuestion: (state, action) => {
        state.currentQuestion = action.payload.question;
        state.questionIndex = action.payload.questionIndex;
        setItem('questionIndex', action.payload.questionIndex);
    },
    setQuestionIndex: (state, action) => {
        const testType = getItem('testType', 'basic');
        state.currentQuestion = state[testType][action.payload];
        state.questionIndex = action.payload;
        setItem('questionIndex', action.payload);
    }
  },
})

// Action creators are generated for each case reducer function
export const { 
    setQuestions,
    setNewCurrentQuestion,
    setQuestionIndex
} = questionsSlice.actions

export default questionsSlice.reducer