import { createSlice } from '@reduxjs/toolkit'
import { getItem, setItem } from './helperFunctions';  

const initialState = {
    locale:getItem('locale', 'en'),
    testType: getItem('testType', 'basic'),
    selectionMethod: getItem('selectionMethod', 'sequential'),
    mode: getItem('mode', 'test'),
}

export const settingsSlice = createSlice({
  name: 'settings',
  initialState,
  reducers: {
    setLocale: (state, action) => {
      state.locale = action.payload;
      setItem('locale', action.payload); 
    },  
    setTestType: (state, action) => {
      state.testType = action.payload;
      setItem('testType', action.payload); 
    },
    setProperty: (state, action) => {
      state[action.payload.prop] = action.payload.value;
      setItem(action.payload.prop, action.payload.value); 
    },
    setSelectionMethod : (state, action) => {
      state.selectionMethod = action.payload;
      setItem('selectionMethod', action.payload); 
    },
    setMode: (state, action) => {
      state.mode = action.payload;
      setItem('mode', action.payload); 
    }
  },
})

// Action creators are generated for each case reducer function
export const { 
    setLocale,
    setTestType,
    setProperty,
    setSelectionMethod,
    setMode,
} = settingsSlice.actions

export default settingsSlice.reducer